.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.login-container {
  position: absolute;
  top: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* This centers vertically */
  align-items: center;
  max-width: 400px;
  max-height: 100vh;
  margin: 0 auto;
  margin-left: 40% !important;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #F0F8FF;
}

h1 {
  margin-bottom: 20px !important;
  color: #007BFF !important;
  font-size: 40px !important;

}

.input-container {
  margin-bottom: 15px;
  text-align: left;
}

.input-container label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  align-items: center;
  color: #333;
}

