.input-container {
  margin-bottom: 15px;
}

.label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #333333;
}

.input-field {
  width: 100%;
  height: 35px !important;
  /* Adjust the height as needed */
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #333;
}

.keep-logged-in {
  display: flex;
  /* align-items: left; */
  /* justify-content: space-between;  */
  margin-bottom: 10px;
}

.keep-logged-in-checkbox {
  margin: 0;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.login-button {
  background-color: #007BFF !important;
  color: white;
  border: darkcyan;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.lng-button {
  background-color: #5B6E83;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-button:hover {
  background-color: #0056B3;
}

.login-message {
  margin-top: 10px;
  margin-bottom: 5px;
  color: #007BFF;
}

.login-message {
  margin-top: 5px;
  /* Adjusted margin for spacing */
  margin-bottom: 10px;
  /* Added margin for spacing */
  color: #007BFF;
}

.forgot-password {
  margin-top: 15px;
  color: #007BFF;
  text-decoration: none;
  display: block;
}

.forgot-password:hover {
  text-decoration: underline;
}

.sign-txt {
  text-align: center;
  margin-top: 15px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  /* Add this line to set the cursor to pointer on hover */
}

/* Optional: Add a hover effect to change the color when hovering */
.sign-txt:hover {
  color: #007bff;
  /* Change to the desired color on hover */
}

.login-button-container {
